import { FC, forwardRef } from 'react';
import { styled } from '../../utils';
import { TwBaseProps, FormattedColor } from '../../types';
import {
  DefaultProps,
  Accordion as MantineAccordion,
  AccordionProps as MantineAccordionProps
} from '@mantine/core';

export interface AccordionProps
  extends TwBaseProps,
    Omit<MantineAccordionProps, keyof DefaultProps> {
  color?: FormattedColor;
}

const Root = styled(MantineAccordion)<MantineAccordionProps>(() => ({}));

export const Accordion: FC<AccordionProps> = forwardRef((props, ref) => {
  return <Root {...props} ref={ref} />;
});
